
export default {
	onLaunch: function() {
		try {
			const id = uni.getStorageSync('id');
			const name = uni.getStorageSync('name');
			if (id) {
				this.$scope.globalData.id = id;
			}
			if (name) {
				this.$scope.globalData.name = name;
			}
		} catch (e) {
			console.log(e);
		}
		console.log('App Launch')
	},
	onShow: function() {
		console.log('App Show')
	},
	onHide: function() {
		console.log('App Hide')
	},
	globalData: {
		name: '',
		id: '',
	}
}
